// eslint-disable-next-line no-unused-vars
import Share from '../models/Share';
import SharingEntry from '../components/SharingEntry';
import ShareTypes from '../mixins/ShareTypes';
export default {
  name: 'SharingList',
  components: {
    SharingEntry
  },
  mixins: [ShareTypes],
  props: {
    fileInfo: {
      type: Object,
      default: () => {},
      required: true
    },
    shares: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  computed: {
    hasShares() {
      return this.shares.length === 0;
    },

    isUnique() {
      return share => {
        return [...this.shares].filter(item => {
          return share.type === this.SHARE_TYPES.SHARE_TYPE_USER && share.shareWithDisplayName === item.shareWithDisplayName;
        }).length <= 1;
      };
    }

  },
  methods: {
    /**
     * Remove a share from the shares list
     *
     * @param {Share} share the share to remove
     */
    removeShare(share) {
      const index = this.shares.findIndex(item => item === share); // eslint-disable-next-line vue/no-mutating-props

      this.shares.splice(index, 1);
    }

  }
};