import { generateUrl } from '@nextcloud/router';
import ActionLink from '@nextcloud/vue/dist/Components/ActionLink';
import SharingEntrySimple from './SharingEntrySimple';
export default {
  name: 'SharingEntryInternal',
  components: {
    ActionLink,
    SharingEntrySimple
  },
  props: {
    fileInfo: {
      type: Object,
      default: () => {},
      required: true
    }
  },

  data() {
    return {
      copied: false,
      copySuccess: false
    };
  },

  computed: {
    /**
     * Get the internal link to this file id
     *
     * @return {string}
     */
    internalLink() {
      return window.location.protocol + '//' + window.location.host + generateUrl('/f/') + this.fileInfo.id;
    },

    /**
     * Clipboard v-tooltip message
     *
     * @return {string}
     */
    clipboardTooltip() {
      if (this.copied) {
        return this.copySuccess ? t('files_sharing', 'Link copied') : t('files_sharing', 'Cannot copy, please copy the link manually');
      }

      return t('files_sharing', 'Copy to clipboard');
    },

    internalLinkSubtitle() {
      if (this.fileInfo.type === 'dir') {
        return t('files_sharing', 'Only works for users with access to this folder');
      }

      return t('files_sharing', 'Only works for users with access to this file');
    }

  },
  methods: {
    async copyLink() {
      try {
        await this.$copyText(this.internalLink); // focus and show the tooltip

        this.$refs.copyButton.$el.focus();
        this.copySuccess = true;
        this.copied = true;
      } catch (error) {
        this.copySuccess = false;
        this.copied = true;
        console.error(error);
      } finally {
        setTimeout(() => {
          this.copySuccess = false;
          this.copied = false;
        }, 4000);
      }
    }

  }
};