import { generateUrl } from '@nextcloud/router';
import { basename } from '@nextcloud/paths';
import Avatar from '@nextcloud/vue/dist/Components/Avatar';
import ActionButton from '@nextcloud/vue/dist/Components/ActionButton';
import ActionLink from '@nextcloud/vue/dist/Components/ActionLink';
import ActionText from '@nextcloud/vue/dist/Components/ActionText'; // eslint-disable-next-line no-unused-vars

import Share from '../models/Share';
import SharesMixin from '../mixins/SharesMixin';
import SharingEntrySimple from '../components/SharingEntrySimple';
export default {
  name: 'SharingEntryInherited',
  components: {
    ActionButton,
    ActionLink,
    ActionText,
    Avatar,
    SharingEntrySimple
  },
  mixins: [SharesMixin],
  props: {
    share: {
      type: Share,
      required: true
    }
  },
  computed: {
    viaFileTargetUrl() {
      return generateUrl('/f/{fileid}', {
        fileid: this.share.viaFileid
      });
    },

    viaFolderName() {
      return basename(this.share.viaPath);
    }

  }
};